'use client';
import React, { useState, useEffect, useRef } from 'react';
import { motion, useAnimation } from 'framer-motion';
import Image from 'next/image';

const companyServices = [[
    {
        id: '1',
        heading: 'IT Infrastructure',
        headingDiscp: 'End-to-end solutions for network management, cloud integration, and infrastructure optimization.',
    }
],
[
    {
        id: '2',
        heading: 'Digital Transformation Consulting',
        headingDiscp: 'Helping organizations transition seamlessly into the digital age, boosting efficiency and innovation.',
    }
],
[
    {
        id: '3',
        heading: 'Quality Assurance & Monitoring',
        headingDiscp: 'Reliable surveillance and monitoring to ensure your systems perform optimally, reducing risks and downtime.',
    }
]]
const companyInfraStructure = [
    {
        id: '1',
        image: '/gray.png',
        label: 'Network Optimization',
        discp: 'Streamlining network performance for reliable connectivity.'
    },
    {
        id: '2',
        image: '/yellow.png',
        label: 'Cloud Integration',
        discp: 'Seamless cloud adoption for enhanced flexibility.'
    },
    {
        id: '3',
        image: '/gray.png',
        label: 'Data Center Solutions',
        discp: 'Efficient data center management and support.'
    },
    {
        id: '4',
        image: '/yellow.png',
        label: 'System Scalability',
        discp: 'Scalable infrastructure to support business growth.'
    },
];

export default function CompanyStructure() {
    const [currentContent, setCurrentContent] = useState(0);  // State to track the current heading
    const controls = useAnimation(); // Animation controls for the heading
    const sectionRefs = useRef<(HTMLDivElement | null)[]>([]); // To store references of each section
    const [scrolling, setScrolling] = useState(false);

    const handleScroll = () => {
        const currentTop = window.scrollY + window.innerHeight / 2;

        // Find the section closest to the middle of the screen
        for (let i = 0; i < sectionRefs.current.length; i++) {
            const section = sectionRefs.current[i];
            if (section) {
                const { offsetTop, offsetHeight } = section;
                if (currentTop >= offsetTop && currentTop < offsetTop + offsetHeight) {
                    setCurrentContent(i);  // Update the content based on scroll position
                    break;
                }
            }
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    useEffect(() => {
        // Trigger animations when content is changed
        controls.start({
            opacity: 1,
            y: 0,
            transition: { duration: 0.5 },
        });
    }, [currentContent]);
    return (
        <>
            <div className='grid  px-4 md:px-12 lg:px-24  content-center '>
                <div className='grid grid-cols-1 lg:grid-cols-2 justify-between lg:gap-28'>

                    <div className='flex flex-col py-12 lg:py-0'>
                        <motion.h1
                            className='text-3xl md:text-4xl lg:text-4xl font-medium'
                            animate={controls}
                            initial={{ opacity: 0, y: -50 }}
                        >
                            {companyServices[currentContent]} {/* Dynamically change heading based on scroll */}
                        </motion.h1>
                        {/* <h1 className='text-3xl md:text-4xl lg:text-4xl font-medium  '>IT Infrastructure <br />  Management</h1> */}
                        <h1 className='text-sm md:text-md lg:text-lg  font-medium text-[#00000099] pt-4 pb-16 '>End-to-end solutions for network management, cloud integration, and infrastructure optimization.</h1>
                        <Image
                            src="/ITeqpt.jpeg"
                            width={487}
                            height={311}
                            className="lg:w-96 lg:h-56  md:w-96 md:h-56 rounded-[40px] object-cover"
                            alt="Hero section image"
                        />
                    </div>
                    <div className='grid grid-cols-1 items-center justify-center gap-8 md:gap-x-10 md:gap-y-7 lg:gap-x-20 lg:gap-y-5 md:grid-cols-1'>
                        {companyInfraStructure.map((structure) => {
                            return (
                                <div
                                    key={structure.id}
                                    className=" flex flex-row    "
                                >
                                    <Image width={106} height={106} alt='IT Imge' src={structure.image} className='w-20 h-20 rounded-3xl' />
                                    <div className='flex flex-col ml-4 lg:ml-8 justify-center '>
                                        <p className="text-xs md:text-xl lg:text-lg font-semibold ">{structure.label}</p>
                                        <p className="text-xs md:text-sm lg:text-xs font-medium text-[#00000099] py-3">{structure.discp}</p>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    )
}
